import React    from 'react'
import { Link } from 'react-router'

import Koan     from './Koan'
import Toc      from './Toc'

const meditation =
  { description: [ ' true .' ]
  , code:        [ { hasInputField: true
                   , text:          'const isReady = _____'
                   }
                 , { hasInputField: false
                   , text:          ''
                   }
                 , { hasInputField: false
                   , text:          'assert.isTrue( isReady )'
                   }
                 ]
}
const next = { category: 'assert', id: 0 }

const Greetings = () =>
  <div className="Greetings">
    <p className="Greetings-welcome">JS Koans JavaScript   
     .   JavaScript      
     ,       .    
         .</p>

    <Link className="Greetings-startButton" to='/assert/0'></Link>

    <Toc />
  </div>

export default Greetings



